<template>
  <div v-if="album" class="total-status-container f-row-between">
    
    <div
      class="cover"
      :style="{ 'background-image': `url( ${album.cover} )` }"
    >
      <span class="album-type">
        {{ album.contentType | fltTemplateName }}
      </span>
      <span v-if="album.lastPlayTime" class="recent-space">
        {{ album.lastPlayTime | fltTimeInterval }}听过
      </span>
    </div>
    
    <div class="content f-col-between">
      <div class="title">
        <p class="main-title two-ellipsis">
          {{ album.name }}
        </p>
        <p class="sub-title one-ellipsis">
          {{ album.intro }}
        </p>
      </div>
      <div class="detail f-row-between">
        <p class="params f-c">
          <span v-if="album.isEndUpdate">
            {{ album.totalItemCount }}节全
          </span>
          <span v-else>
            更新至{{ album.itemCount }}节
          </span>
          <span class="split-inline-1">
            |
          </span>
          <span>
            {{ album.playUv }}人听过 
          </span>
          <!-- <span class="split-inline-1">
            |
          </span> -->
          <!-- <span>
            已听25%
          </span> -->
        </p>
        <p v-if="showPayStatus" class="status">
          <span v-if="album.chargeType == 0" class="not-vip-free">
            免费
          </span>
          <span v-else-if="album.chargeType == 1" class="vip-free">
            会员畅听
          </span>
          <span v-else-if="album.isBuy" class="had-bought">
            已购买
          </span>
          <span v-else class="not-bought">
            <span class="now-price">
              ￥{{ album.spuInfo.price | fltToDecimal2 }}
            </span>
            <span v-if="album.spuInfo.orgiPrice !== album.spuInfo.price" class="old-price">
              ￥{{ album.spuInfo.orgiPrice | fltToDecimal2 }}
            </span>
          </span>
        </p>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  props: {
    album: {
      type: Object,
      default: () => null
    },
    // ? 是否显示专辑的付费状态
    showPayStatus: {
      type: Boolean,
      default: true
    }
  },

  data() {
    return {
      
    }
  }
}
</script>

<style lang="scss" scoped>
.total-status-container {
  width: 100%;

  .cover {
    box-shadow: 0 0 1px #dfdfdf inset;
    width: 88px;
    height: 117px;
    border-radius:5px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    
    .album-type {
      background: rgba(0,0,0,0.4);
      border-top-right-radius: 5px;
      border-bottom-left-radius: 5px;
      font-size: 10px;
      color: rgba(255,255,255,1);
      padding: 3px 5px;
      position: absolute;
      top: 0;
      right: 0;
    }
    .recent-space {
      background: rgba(0,0,0,0.4);
      border-top-right-radius: 5px;
      border-bottom-left-radius: 5px;
      font-size: 10px;
      color: rgba(255,255,255,1);
      padding: 3px 5px;
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }

  .content {
    width: calc(100% - 103px);
    
    .title {
      .main-title {
        font-size: 16px;
        font-weight: 600;
        color: rgba(51,51,51,1);
      }
      .sub-title {
        margin-top: 14.5px;
        font-size: 14px;
        color: rgba(153,153,153,1);
        line-height: 1.5;
      }
    }

    .detail {
      .params {
        font-size: 12px;
        color: rgba(187,187,187,1);

        .split-inline-1 {
          margin: 0 3px;
        }
      }
      .status {
        .not-vip-free {
          font-size: 12px;
          color: #7FB745;
        }
        .vip-free {
          font-size: 12px;
          color: rgba(126,182,254,1);
        }
        .had-bought {
          font-size: 12px;
          color: #aaa;
        }
        .not-bought {
          .now-price {
            color: #EF3C3C;
            font-size: 15px;
          }
          .old-price {
            color: #BBBBBB;
            font-size: 11px;
            text-decoration: line-through;
          }
        }
      }
    }
  }
}
</style>


